//,,
function scrollToDem(t){

    var scrollTo;

    if (t instanceof Number){
        scrollTo = t
    } else {
        var $r = $('#room'),
            scrollTopNow = $r[0].scrollTop,
            height = t[0].offsetHeight;

        scrollTo = scrollTopNow + height;
    }

    $r.animate({scrollTop: scrollTo}, 200)
}

//Vue
function createVue(obj){

    var el = '#q' + obj.itemId;

    vues.push(new Vue({

        el: el,

        data: obj,

        methods: funcs
    }));
    total += 1;
    //
    scrollToDem($(el));
}

